import { FETCH_CITIES,CHECK_DISTANCE } from "./types";
import myserver from "../apis/server";

export const fetchCities = () => async dispatch => {
  const response = await myserver.get("/cities");

  dispatch({ type: FETCH_CITIES, payload: response.data });
};

export const checkDistance = (from,to) => async dispatch => {
  const response = await myserver.get(`/distance/${from}/${to}`);

  dispatch({ type: CHECK_DISTANCE, payload: response.data });
};
