import React from "react";
import { connect } from "react-redux";
import { fetchCities } from "../actions";
import Picker from "./Picker";

class App extends React.Component {
  componentDidMount = () => {
    this.props.fetchCities();
  }
  render() {
    return (
      <div className="App">
        <Picker />
      </div>
    );
  }
}

export default connect(
  null,
  { fetchCities }
)(App);
